.infoBarWrapper {
    display: flex;
    width: 100%;
    background: rgb(219, 233, 28);
    background: linear-gradient(90deg, rgba(219, 233, 28, 1) 3%, rgba(94, 186, 43, 1) 89%);
}

.infoBarInfo {
    display: flex;
}

.infoBarInfo li {
    margin: 0 15px 0px 0px;
    list-style-type: none;
    border-right: 2px solid black;
    padding: 0px 15px 0px 0px;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0.5px;
}

@media (max-width: 1100px) {

    .infoBarWrapper {
        display: flex;
        max-width: 700px;
        align-items: center;
        margin: auto;
        background: rgb(219, 233, 28);
        background: linear-gradient(90deg, rgba(219, 233, 28, 1) 3%, rgba(94, 186, 43, 1) 89%);
    }

    .infoBar {
        display: flex;
        max-width: 700px;
        margin: auto;
    }
}