.imageContainer:nth-child(2) {
    max-width: 400px;
    height: 400px;
    margin-left: 50px;
    margin-right: 50px;
} */

.imageContainer {
    margin: auto;
}

.imageContainer:nth-child(2){
    margin-left: 60px;
    margin-right: 60px;
}


@media (max-width: 1100px) {
    .productPageWrapper {
        display: flex;
        flex-direction: column;
        max-width: 1000px;
        height: 100%;
        margin: auto;
    }

    .productsTemplateWrapper {
        display: flex;
        align-items: center;
        flex-direction: column;
        max-width: 800px;
        margin: 130px auto auto auto;
        height: auto;
    }

    .imageContainer img {
        width: 248px;
        height: 260px;
        align-self: center;
        border-radius: 15px 0px 0px 15px;
        margin-right: 155px;
    }

    .imageContainer {
        display: flex;
        width: 700px;
        margin: auto;
        margin: 15px;
        padding: 15px;
        height: 250px;
    }

    .imageContainer:nth-child(2){
        margin-left: 15px;
        margin-right: 15px;
    }
}
