.contactWrapper {
    max-width: 1400px;
    height: 900px;
    margin: auto;
    display: flex;
    flex-direction: column;
}

.image {
    position: relative;
    float: right;
    width: 300px;
    height: 150px;
    margin: auto;
}

.liner {
    position: relative;
    border-top: 2px solid black;
    width: 90%;
    top: 80px;

    /* eight: 2px; h */
    /* padding: 1px; */
}


.mainContentWrapper {
    display: flex;
    width: 1000px;
    margin: auto;
    max-height: 900px;
    margin-top: 120px;
}

.contactsWrapper {
    -webkit-text-stroke: 0.8px black;
    color: rgba(213, 171, 78, 1);
    display: flex;
    flex-direction: column;
    width: 100%;
    /* height: 700px; */
    margin-left: 10px;
    font-size: 30px;
    text-align: start;
    align-content: center;
    line-height: 20.5px;

}

.contactsWrapper h1 {
    font-size: 30px;
    font-weight: 300;
}

.imageWrapper {
    width: 469px;
    height: 367px;
    border-radius: 25px;
    margin-right: 10px;
    flex-basis: 1;
    box-shadow: 12px 12px 24px 10px rgba(0, 0, 0, 0.37);
}


@media (max-width: 1100px) {
    .mainContentWrapper {
        max-width: 700px;
        display: flex;
        flex-direction: column;
        margin: auto;
    }


    .contactsWrapper {
        display: flex;
        max-width: 100%;
        margin: auto;
        line-height: 10.5px;
        font-size: 20px;
        align-items: center;
    }
}